/* Container für die gesamte App */
.app-container {
  display: flex;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  height: 100vh;
  margin: 0;
  background: black;
  color: white;
}

/* Linke Sidebar (Eingabe) */
.sidebar {
  width: 30%;
  background: linear-gradient(to right, rgb(19, 18, 18), rgb(50, 50, 50));
  color: white;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  box-shadow: 3px 0 15px rgba(0, 0, 0, 0.6); /* Schatten nach rechts */
  overflow-y: auto;
  border-right: 2px solid rgba(255, 255, 255, 0.1); /* Dezenter Rand rechts */
}

/* Überschrift */
.sidebar h2 {
  font-size: 1.5rem;
  color: #08fdd8;
  text-align: left;
  margin-bottom: 1rem;
  border-bottom: 2px solid rgba(8, 253, 216, 0.4); /* Linie unter der Überschrift */
  padding-bottom: 0.5rem;
}

/* Scroll-Leiste */
.sidebar::-webkit-scrollbar {
  width: 8px; /* Breite der Scrollbar */
}

.sidebar::-webkit-scrollbar-thumb {
  background-color: rgba(8, 253, 216, 0.6); /* Türkise Farbe */
  border-radius: 4px;
}

.sidebar::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05); /* Leichter Kontrast für den Scroll-Track */
}

/* Input Container */
.coordinate-input {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

/* Einzelne Zeile für Tänzer */
.input-row {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.5rem;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.10); /* Subtiler Kontrast */
}

/* Tänzer-Info (Name und Index) */
.taenzer-info {
  flex: 1;
  font-size: 0.9rem;
  color: #08fdd8;
  font-weight: bold;
}

/* Input-Gruppe (X und Y mit Label) */
.input-group {
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

/* Labels für Achsen */
.axis-label {
  font-size: 0.9rem;
  color: white;
}

/* Eingabefelder */
.input-group input {
  width: 50px;
  padding: 0.3rem;
  font-size: 0.9rem;
  border: 1px solid #08fdd8;
  border-radius: 4px;
  background-color: black;
  color: white;
  transition: all 0.3s ease;
}

.input-group input:focus {
  outline: none;
  box-shadow: 0 0 5px #08fdd8;
}

/* Hover-Effekt für Zeilen */
.input-row:hover {
  background: rgba(8, 253, 216, 0.1);
}

/* 3D Szene */
.board-container {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1f1f1f; /* Gleicher Hintergrund für futuristisches Aussehen */
}